import React, { Component } from 'react';

class Technologies extends Component{
    render(){
        return(
            <div className="intro-tech">
                <h1 className="banner-content">Technologies:</h1>
                <div className="tech-container">
                    <div className="tech-icon">React</div>
                    <div className="tech-icon">Node</div>
                    <div className="tech-icon">PostgreSQL</div>
                    <div className="tech-icon">Angular</div>
                    <div className="tech-icon">HTML5</div>
                    <div className="tech-icon">CSS</div>
                    <div className="tech-icon">EXPRESS</div>
                    <div className="tech-icon">AWS</div>

                </div>
            </div>
        )
    }

}

export default Technologies