import React, { Component, createElement } from 'react';
//Data
import timedata from "./timedata.json"

class TimeSlot extends Component{    


    render(){
      const { month, id } =this.props
      const data = timedata.filter((month) => {
        if (month.id === id){
          return month
        }
      })
      const events = data[0].events

      const reflect = (eventID, allContent, url,) => {
        const body = document.getElementById(eventID+" reflection")
        const children = document.getElementsByClassName("reflection-content-container")
        //console.log(children.length)
        const exist = document.getElementById(`${eventID}-content`)
        if (exist){
          exist.remove()
        }
        else{
          const contentContainer = document.createElement('div')
          contentContainer.className = 'reflection-content-container'
          contentContainer.setAttribute('id',`${eventID}-content`)
          body.appendChild(contentContainer)
          const count = 1;
          const filteredContent = allContent.split('<')
          filteredContent.forEach(content => {
            if (content !== ''){
              const element = content.slice(0,2)
              const entry = content.slice(3)
              if (element === 'a>'){
                const reflection = document.createElement('a')
                const link = content.slice(2)
                reflection.className = `reflection-content reflection-style-link item-${count}`
                reflection.textContent = link
                reflection.href = url
                contentContainer.appendChild(reflection) 
              }
              else{
                const reflection = document.createElement(element)
                reflection.className = `reflection-content reflection-style-${element} item-${count}`
                reflection.textContent = entry
                contentContainer.appendChild(reflection) 
                }
              }         
            })
          }
                        
      }
      return(
        <div id="time-slot">
          <div className="month-container">
            <div id="month">{month}</div>
          </div>
          <div className="dates-container">
            <div className="event-container">
              {events.map((event) => {                
                return <div id="event" onClick={() => reflect(event.id, event.reflection,event.projectURL,event.id)}>
                          <a className="event-header" >
                            <div id={"date"}>
                              <div className="cal-month">{event.month}</div>
                              <div className="cal-day">{event.day}</div>
                            </div>
                            <div className={"event-info"}>
                              <div className="event-event">{event.event}</div>
                              <div className="reflection" id={event.id+" reflection"}></div>
                            </div>
                          </a>
                       </div>
              })}
            </div>
          </div>

        </div>
        );
    }
}


export default TimeSlot


