import React, { Component } from 'react';

class Projects extends Component{
    render(){
        return(
        <div className="section-container">
            <div className="section-header">Projects:</div>
            <div className="projects-container">
                <div id="project">
                    <div id="project-id"><div id="project-num"></div></div>
                    <div id="project-title"><div id="project-num">Title</div></div>
                    <div id="project-tech"><div id="project-num">Main Focus</div></div>
                </div>
                <div id="project">
                    <div id="project-id"><div id="project-num">#2:</div>Landing Page Project</div>
                    <div id="project-title"><a href="https://herman-woo.github.io/Cities/">'Most Populous Cities'</a></div>
                    <div id="project-tech">JavaScript</div>
              </div>
            </div>
        </div>
        )
    }

}

export default Projects


